.profile {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
  }
  .profile img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
  }
  .profile div {
    margin: 0 20px;
  }
  .profile h2 {
    font-size: 30px;
    margin: 0;
  }
  .profile p {
    margin: 0;
  }