/* BlogPage.css */

.headline {
    text-align: center;
  }
  
  .blog-list {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    padding: 20px;
  }
  
  .blog-post {
    background-color: #f4f4f4;
    padding: 20px;
    border-radius: 8px;
  }
  
  .blog-post h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .blog-post .date {
    color: #888;
    margin-bottom: 10px;
  }
  
  .blog-post p {
    line-height: 1.5;
  }
  