/* reset.css */

/* 全ての要素のマージンとパディングをリセットする */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* リストスタイルをリセットする */
  ul,
  ol {
    list-style: none;
  }
  
  /* リンクのデフォルトのスタイルをリセットする */
  a {
    text-decoration: none;
    color: inherit;
  }
  
  /* その他のリセットスタイルを追加する */
  /* ... */
  