/* ContactPage.css */

.headline {
    text-align: center;
  }
  
  .contact-info {
    max-width: 600px;
    margin: 0 auto;
  }
  
  .contact-form {
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 10px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button[type="submit"] {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .contact-details {
    margin-top: 20px;
  }
  
  .contact-details h2 {
    margin-bottom: 10px;
  }
  
  .contact-details p {
    margin-bottom: 5px;
  }
  
  /* ContactPage.css */

.contact-form textarea {
    resize: vertical; /* テキストエリアの垂直方向のリサイズを有効にする */
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .contact-form button[type="submit"] {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    margin-top: 10px;
    cursor: pointer;
  }
  
  .contact-details a:hover {
    color: #888;
  }
  