/* Header.css */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    background-color: #f2f2f2;
  }
  
  .header-logo {
    font-size: 24px;
    font-weight: bold;
    color: #333;
  }
  
  .header-nav {
    display: flex;
  }
  
  .header-nav-link {
    margin-left: 16px;
    color: #666;
    text-decoration: none;
  }
  
  .header-nav-link:hover {
    color: #000;
  }
  ul{
    display: flex;
  }
  li{
    margin-left: 16px;
    color: #666;
    text-decoration: none;
}
  li:hover{
    color: #000;
    transition: all 0.5s;
    transform: scale(1.2);
  }

  