/* SkillsPage.css */
/* 追加した部分 */

.headline {
  text-align: center;
}

.skills-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column; /* 縦方向に並べる */
  align-items: center; /* 中央に配置 */
}

.skills-list li {
  margin-bottom: 10px;
  display: flex;
  justify-content: center; /* 水平方向に中央寄せ */
  align-items: flex-start; /* 上寄せ */
  background-color: rgb(205, 205, 213);
  padding: 1rem;
}

.item-container {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* 左寄せ */
  width: 300px; /* 幅を固定 */
}

.icon-container {
  margin-right: 10px;
  flex-shrink: 0; /* アイコンの幅を固定 */
}

.icon {
  width: 24px;
  height: 24px;
}

.content-container {
  display: flex;
  flex-direction: column;
  /* 右寄りに配置 */
  justify-content: flex-end;

  
}

.technology {
  font-weight: bold;
  overflow: hidden; /* テキストのオーバーフローを隠す */
  text-overflow: ellipsis; /* テキストがオーバーフローした場合に省略符号で表示 */
  white-space: nowrap; /* テキストを折り返さずに表示 */
}

.rating {
  color: #888;
  margin-top: 4px; /* 評価の上側に余白を追加 */
}

/* ホバー時の効果を向無効にする */
.skills-list li　[disabled]{
  pointer-events: none;
}
